import React, { Component } from 'react'
import { Navigate } from 'react-router-dom';
import {
    Container, Button, Form, FormGroup, Label, Input, Alert, Row, Col
} from 'reactstrap';
import image from '../../logo.png';
import axios from 'axios';

export default class AdminLogin extends Component {
    state = {
        email: '',
        password: '',
        msg: null,
        forgot: false,
        isAuthenticated: localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')).user.admin : false
    }
    onChange = (e) => {
        this.setState({
            [e.target.name]:e.target.value
        })
    }
    toggleForgotPass = () => {
        this.setState({
            forgot: !this.state.forgot
        });
    }
    onSubmit = (e) => {
        e.preventDefault();

        const {email, password} = this.state;

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }
    
        // request body
        const body = JSON.stringify({ email, password }); 
        axios.post('/api/login', body, config)
            .then(res => {
                if (res.data.user.admin) {
                    localStorage.setItem('user', JSON.stringify(res.data));
                    localStorage.setItem('token', (res.data.token));
                    this.setState({ 
                        msg: null,
                        isAuthenticated: localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')).user.admin : false
                    });
                } else {
                    this.setState({ 
                        msg: "Invalid account type",
                        isAuthenticated: localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')).user.admin : false
                    });
                }
                // window.location.reload(false);
            })
            .catch((err) => {
                this.setState({ 
                    msg: err.response.data.msg,
                    isAuthenticated: localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')).user.admin : false
                 });
                localStorage.removeItem('user');
                localStorage.removeItem('token');
            })
    }
  render() {
    return (
      <div>
          {this.state.isAuthenticated ? <Navigate to="/adminmain" /> : null}
          <Container>
              <Row style={{paddingTop: '15px'}}>
                  <Col sm="1" md='2' lg="3"></Col>
                  <Col sm="10" md='8' lg="6" style={{backgroundColor: 'white'}}>
                      <Row style={{marginTop: '15px'}}>
                        <Col xs="3">
                            <img src={image} className="rounded img-fluid" style={{margin: '5%'}} alt="logo"></img>
                        </Col>
                        <Col xs="9">
                            <h1 style={{textAlign: 'center', marginTop: '20px'}}>Smart Office System</h1>
                            <h3 style={{textAlign: 'center'}}>Admin Portal</h3>
                        </Col>
                      </Row>
                    <Row>
                        <Col sm="0" md="2"></Col>
                        <Col sm="12" md="8">
                            { this.state.forgot ? 
                            (
                                <div>
                                    <h4 style={{textAlign: 'center', marginTop: '10px'}}>
                                        Please contact system administrator at khalil.zayed@ontariotechu.net.
                                    </h4>
                                    <Button color="dark"
                                            style={{marginTop: '2rem', marginBottom: '50px'}}
                                            block
                                            onClick={this.toggleForgotPass}
                                        >
                                            Back</Button>
                                </div>
                            )
                            :
                            (
                                <div>
                                    <h4 style={{textAlign: 'center', marginTop: '10px'}}>Log In</h4>
                                    { this.state.msg ? <Alert color="danger">{this.state.msg}</Alert> : null }
                                    <Form onSubmit={this.onSubmit}>
                                        <FormGroup>
                                            <Label for="email">Email</Label>
                                            <Input 
                                                type="email"
                                                name="email"
                                                id="email"
                                                placeholder="Email"
                                                className="mb-3"
                                                onChange={this.onChange}
                                                required={true}
                                            />
                                            <Label for="password">Password</Label>
                                            <Input 
                                                type="password"
                                                name="password"
                                                id="password"
                                                placeholder="Password"
                                                className="mb-3"
                                                onChange={this.onChange}
                                                required={true}
                                            />
                                            <a href="#" onClick={this.toggleForgotPass} style={{marginLeft: '33%'}}>Forgot password?</a>
                                            <Button
                                                color="dark"
                                                style={{marginTop: '2rem', marginBottom: '50px'}}
                                                block
                                            >

                                                Login</Button>
                                        </FormGroup>
                                    </Form>
                                </div>
                            )
                            }
                        </Col>
                        <Col sm="0" md="2"></Col>
                    </Row>
                  </Col>
                  <Col sm="1" md='2' lg="3"></Col>
              </Row>
        </Container>
      </div>
    )
  }
}
