import React, { Component } from 'react'
import { Navigate, Link } from 'react-router-dom';
import {
    Row, Col, Dropdown, DropdownItem, DropdownToggle, DropdownMenu
} from 'reactstrap';
import logo_sm from '../../logo-sm.png';

export default class SuperUserNav extends Component {
    state = {
        isAuthenticated: localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')).user.superuser : false,
        isOpen: false
    }
    toggle = () => {
        this.setState({
            isOpen: !this.state.isOpen
        })
    }
    redirect = () => {
        localStorage.removeItem('user');
        localStorage.removeItem('token');
        this.setState({
            isAuthenticated: localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')).user.superuser : false,
        })
    }
  render() {
    return (
      <div>
        {this.state.isAuthenticated ? null : (<Navigate to="/superuser" />)}
        <Row style={{marginTop: '5px'}}>
            <Col xs="3" md="2">
                <img src={logo_sm} className="rounded img-fluid" style={{}} alt="logo"></img>
            </Col>
            <Col xs="5" md="7" className='my-auto'>
                <h3 style={{textAlign: 'center'}}>Smart Office System</h3>
            </Col>
            <Col xs="4" md="3" className='justify-content-center text-center my-auto'>
                <span>Hello {localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')).user.first_name : null}</span>
                <br/>
                <Dropdown isOpen={this.state.isOpen} toggle={this.toggle}>
                    <DropdownToggle className='buttonclr' caret>
                        Menu
                    </DropdownToggle>
                    <DropdownMenu end>
                    <DropdownItem tag={Link} to="/superusermain">Admins</DropdownItem>
                    <DropdownItem tag={Link} to="/superuserlogs">Activity log</DropdownItem>
                    <DropdownItem divider />
                    <DropdownItem onClick={this.redirect}>Logout</DropdownItem>
                    </DropdownMenu>
                </Dropdown>
            </Col>
        </Row>
      </div>
    )
  }
}
