import React, { Component } from 'react'
import { Navigate, Link } from 'react-router-dom';
import {
    Container, Button, Row, Col, Table, Modal,
    ModalHeader, ModalBody, Form, FormGroup, Label, Input, Alert, ModalFooter
} from 'reactstrap';
import axios from 'axios';
import AdminNav from './AdminNav';
import { MDBDataTableV5 } from 'mdbreact';

export default class AdminLogs extends Component {
    state = {
        isAuthenticated: localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')).user.admin : false,
        rooms: [],
        selectedRoom: {},
        logs: [],
        msg: null
    }
    componentDidMount() {
        this.getDoors();
    }
    getDoors = () => {
        if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
            // development build code
            axios.defaults.baseURL = 'http://localhost:5000/';
        } else {
            // production build code
        }
        axios({
            method: 'post',
            url: `/api/rooms/rooms/all`,
            headers: {
                'x-auth-token': localStorage.getItem('token'),
                'Content-Type': 'application/json'
            }
          }).then(res => {
            console.log(res.data);
            this.setState({
                rooms: res.data
            })
          })
          .catch(err => {
              if(err.response.data.redirect) this.redirect();
            //   if (err.response.status == 400) this.redirect();
          })
    }
    onChange = (e) => {
        this.setState({
            selectedRoom: e.target.value
        })
        
        axios({
            method: 'post',
            url: `/api/rooms/log/${e.target.value}`,
            headers: {
                'x-auth-token': localStorage.getItem('token'),
                'Content-Type': 'application/json'
            }
          }).then(res => {
            console.log(res.data);
            this.setState({
                logs: res.data
            })
          })
          .catch(err => {
              if(err.response.data.redirect) this.redirect();
            console.log(err)
            // if (err.response.status == 400) this.redirect();
          })
    }
    formatDate = (dateString) => {
        const date = new Date(dateString);
        return date.toDateString() + ' ' + date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds();
        // return date.toDateString()
    }
    redirect = () => {
        localStorage.removeItem('user');
        localStorage.removeItem('token');
        this.setState({
            isAuthenticated: localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')).user.admin : false,
        })
    }
  render() {
      const logs = this.state.logs.map(element => ({
          room: element.room,
          email: element.email,
          status: element.status,
          mess: element.message,
          dateprocessed: this.formatDate(element.date),
          request: (element.property ? "UNLOCK" : "LOCK")
      }))
      console.log(logs);
    const data = {
        columns: [
            {
                label: 'Room',
                field: 'room'
            },
            {
                label: 'Requested by',
                field: 'email'
            },
            {
                label: 'Date',
                field: 'dateprocessed'
            },
            {
                label: 'Status',
                field: 'status',
                // width: 150
            },
            {
                label: 'Request',
                field: 'request',
            },
            {
                label: 'Message',
                field: 'mess',
            }
        ],
        rows: logs
    }
    console.log(data.rows)
    return (
      <div>
        {this.state.isAuthenticated ? null : (<Navigate to="/admin" />)}
        <div>
            <Row style={{paddingTop: '15px'}}>
                <Col sm="0" lg="2"></Col>
                <Col sm="12" lg="8" style={{backgroundColor: 'white'}}>
                    <AdminNav/>
                    <hr/>
                    <Row style={{marginBottom: '10px'}}>
                        <Col style={{marginLeft: '10px'}}>
                            <h4 style={{float: 'left'}}>Activity log</h4>
                        </Col>
                        <Col style={{marginRight: '10px'}}>
                        <Input type="select" id="selectRoom" style={{width: '50%', float: 'right'}} onChange={this.onChange}>
                            <option></option>
                            {this.state.rooms.map(room => 
                                (<option key={room.name} value={room.name}>{room.name}</option>)
                            )}
                        </Input>
                        <Label for="selectRoom" style={{float: 'right', margin: '5px'}}>Select room: </Label>
                        </Col>
                    </Row>

                    {this.state.logs.length !== 0 ? 
                    (<MDBDataTableV5
                        striped
                        bordered
                        hover
                        entriesOptions={[5, 10, 15]}
                        entries={10}
                        pagesAmount={4}
                        data={data}
                        responsive
                    /> )
                    : 
                    (
                        <h4 style={{margin: '20px'}}>Please select a room to proceed.</h4>
                    )
                }
                    

                </Col>
                <Col sm="0" lg="2"></Col>
            </Row>
        </div>
      </div>
    )
  }
}
