import React, { Component } from 'react'
import {BrowserRouter as Router, Route, Routes, Navigate} from 'react-router-dom';
import UserLogin from './components/user/UserLogin'
import UserRooms from './components/user/UserRooms'
import AdminLogin from './components/admin/AdminLogin'
import AdminMain from './components/admin/AdminMain'
import AdminRooms from './components/admin/AdminRooms'
import AdminLogs from './components/admin/AdminLogs'
import SuperUserLogin from './components/superuser/SuperUserLogin'
import SuperUserMain from './components/superuser/SuperUserMain'
import SuperUserLogs from './components/superuser/SuperUserLogs'
import './App.css';
import axios from 'axios';

import 'bootstrap/dist/css/bootstrap.min.css';
// import 'mdbreact/dist/css/mdb.css';
import '@fortawesome/fontawesome-free/css/all.min.css'; 
// import 'bootstrap-css-only/css/bootstrap.min.css';

export default class App extends Component {
  state = {
    redirect: false,
    type: 0
  }
  componentDidMount() {
    if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
        // development build code
        axios.defaults.baseURL = 'http://localhost:5000/';
    } else {
        // production build code
    }
    if (localStorage.getItem('token')) {
      console.log('token check running')
      axios({
        method: 'get',
        url: '/api/login/user',
        headers: {'x-auth-token':localStorage.getItem('token')}
      }).then(res => {
        this.setState({
          redirect: false
        })
      })
      .catch(err => {
        console.log(err)
        const user = localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')) : {};
        localStorage.removeItem('user');
        localStorage.removeItem('token');
        this.setState({
          redirect: true,
          type: user.user.admin ? 1 : (user.user.superuser ? 2 : 0)
        })
      })
      }
  }
  handleNav = () => {
    this.setState({
      redirect: false
    })
    switch(this.state.type) {
      case 1:
        return (<Navigate to="/admin" />);
      case 2:
        return (<Navigate to="/superuser" />);
    }
    return (<Navigate to="/" />);
  }
  render() {
    return (
      <Router>
        <div className='App'>
            {this.state.redirect ? this.handleNav() : null }
          <Routes>
            <Route exact path="/" element={<UserLogin/>} /> 
            <Route path="/rooms" element={<UserRooms/>} /> 
            <Route path="/admin" element={<AdminLogin/>} />
            <Route path="/adminmain" element={<AdminMain/>} />
            <Route path="/adminrooms" element={<AdminRooms/>} />
            <Route path="/adminlogs" element={<AdminLogs/>} />
            <Route path="/superuser" element={<SuperUserLogin/>} />
            <Route path="/superusermain" element={<SuperUserMain/>} />
            <Route path="/superuserlogs" element={<SuperUserLogs/>} />
          </Routes>
        </div>
      </Router>
    )
  }
}
