import React, { Component } from 'react'
import { Navigate } from 'react-router-dom';
import {
    Container, Button, Row, Col, Table, Modal, ModalHeader, ModalBody, Form, FormGroup, Label, Input, Alert, ModalFooter
} from 'reactstrap';
import image from '../../logo.png';
import logo_sm from '../../logo-sm.png';
import axios from 'axios';

export default class UserRooms extends Component {
    state = {
        isAuthenticated: localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')).user.user : false,
        rooms: [],
        selectedRoom: {},
        logs: [],
        modal: false,
        msg: null
    }
    componentDidMount() {
        this.loadRooms();
    }
    loadRooms = () => {
        const email = JSON.parse(localStorage.getItem('user')).user.email;
        if (!process.env.NODE_ENV || process.env.NODE_ENV === 'development') {
            // development build code
            axios.defaults.baseURL = 'http://localhost:5000/';
        } else {
            // production build code
        }
        axios({
            method: 'post',
            url: `/api/users/permission/${email}`,
            headers: {
                'x-auth-token': localStorage.getItem('token'),
                'Content-Type': 'application/json'
            }
          }).then(res => {
            console.log(res.data);
            this.populateTable(JSON.stringify(res.data));
          })
          .catch(err => {
              if (err.response.data.redirect) this.redirect();
          })
    }
    populateTable = (data) => {
        axios({
            method: 'post',
            url: `/api/rooms/${data}`,
            headers: {
                'x-auth-token': localStorage.getItem('token'),
                'Content-Type': 'application/json'
            }
          }).then(res => {
            console.log(res.data);
            this.setState({
                rooms: res.data
            })
          })
          .catch(err => {
            console.log(err.response.msg)
            if (err.response.data.redirect) this.redirect();
          })
    }
    selectRoom = (room) => {
        this.setState({
            selectedRoom: room
        })
        this.toggle();
        this.getLogs(room.name);
    }
    toggle = () => {
        this.setState({
            modal: !this.state.modal,
            msg: null
        });
    }
    getLogs = (data) => {
        axios({
            method: 'post',
            url: `/api/rooms/logs/${data}`,
            headers: {
                'x-auth-token': localStorage.getItem('token'),
                'Content-Type': 'application/json'
            }
          }).then(res => {
            console.log(res.data);
            this.setState({
                logs: res.data
            })
          })
          .catch(err => {
            console.log(err)
            if (err.response.data.redirect) this.redirect();
          })
    }
    switchLock = () => {
        var room = JSON.stringify({
            room: this.state.selectedRoom,
            user: {
                first_name: JSON.parse(localStorage.getItem('user')).user.first_name,
                last_name: JSON.parse(localStorage.getItem('user')).user.last_name,
                email: JSON.parse(localStorage.getItem('user')).user.email
            }
        });
        axios({
            method: 'post',
            url: `/api/rooms/switch/${room}`,
            headers: {
                'x-auth-token': localStorage.getItem('token')
            }
          }).then(res => {
            var selected = res.data.doc;
            var log = res.data.log;
            console.log(selected);
            var rooms = this.state.rooms;
            var foundIndex = rooms.findIndex(x => x.name == this.state.selectedRoom.name);
            rooms[foundIndex] = selected;
            this.setState({
                selectedRoom: selected,
                rooms: rooms,
                logs: [
                    log,
                    ...this.state.logs
                ],
                msg: null
            })
          })
          .catch(err => {
              this.setState({
                  msg: err.response.data.msg
              })
            console.log(err.response.data.msg)
            if (err.response.data.redirect) this.redirect();
          })
    }
    formatDate = (dateString) => {
        const date = new Date(dateString);
        return date.toDateString() + ' ' + date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds();
        // return date.toDateString()
    }
    redirect = () => {
        localStorage.removeItem('user');
        localStorage.removeItem('token');
        this.setState({
            isAuthenticated: localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')).user.user : false,
        })
    }
  render() {
    return (
      <div>
        {this.state.isAuthenticated ? null : (<Navigate to="/" />)}
        <Container>
            <Row style={{paddingTop: '15px'}}>
                <Col sm="0" lg="2"></Col>
                <Col sm="12" lg="8" style={{backgroundColor: 'white'}}>
                    <Row style={{marginTop: '5px'}}>
                        <Col xs="3" md="2">
                            <img src={logo_sm} className="rounded img-fluid" style={{}} alt="logo"></img>
                        </Col>
                        <Col xs="5" md="7" className='my-auto'>
                            <h3 style={{textAlign: 'center'}}>Smart Office System</h3>
                        </Col>
                        <Col xs="4" md="3" className='justify-content-center text-center my-auto'>
                            <span>Hello {localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')).user.first_name : null}</span><br/>
                            <Button className='buttonclr' onClick={this.redirect}>LOG OUT</Button>
                        </Col>
                    </Row>
                    <hr/>
                    <h4>Rooms</h4>
                    {this.state.rooms ? 
                        (
                        <Table className='table-hover table-bordered table-striped' responsive>
                            <thead className='table-dark'>
                                <tr>
                                    <th scope="col">#</th>
                                    <th scope="col">Name</th>
                                    <th scope="col">Building</th>
                                    <th scope="col">Status</th>
                                </tr>
                            </thead>
                            <tbody>
                                {this.state.rooms.map((element, index) => (
                                    <tr key={element.name} onClick={this.selectRoom.bind(this, element)}>
                                        <th scope="row">{index+1}</th>
                                        <td>{element.name}</td>
                                        <td>{element.building}</td>
                                        {element.unlocked ? (<td style={{color: 'green'}}>UNLOCKED</td>) : (<td style={{color: 'red'}}>LOCKED</td>)}
                                        
                                    </tr>
                                ))}
                            </tbody>
                        </Table>
                        )
                        :
                        (<h3>No rooms available</h3>)
                    }
                </Col>
                <Col sm="0" lg="2"></Col>
            </Row>
            <Modal
                isOpen={this.state.modal}
                toggle={this.toggle}
            >
                <ModalHeader toggle={this.toggle}>{this.state.selectedRoom.name}</ModalHeader>
                <ModalBody>
                    { this.state.msg ? <Alert color="danger">{this.state.msg}</Alert> : null }
                    <span style={{fontSize: '1.5rem', marginLeft: '5%', marginTop: '5px'}}> STATUS: </span>
                    {this.state.selectedRoom.unlocked ? 
                    (
                        <span>
                            <span style={{color: 'green', fontSize: '1.5rem', marginLeft: '5%', marginTop: '5px'}}> UNLOCKED </span>
                            <Button style={{marginLeft: '5%'}} color="danger" onClick={this.switchLock}>LOCK</Button>
                        </span>
                    ) 
                    : 
                    (
                        <span>
                            <span style={{color: 'red', fontSize: '1.5rem', marginLeft: '5%', marginTop: '5px'}}> LOCKED </span>
                            <Button style={{marginLeft: '5%'}} color="success" onClick={this.switchLock}>UNLOCK</Button>
                        </span>
                    )
                    }
                    <hr/>
                    {this.state.logs.length ? 
                    (
                        <div>
                            <span style={{fontSize: '1.5rem', marginLeft: '2%', marginTop: '5px', marginBottom: '10px'}}>Recent activity</span>
                            <Table style={{marginTop:'2%'}} className='table-bordered table-striped' responsive>
                                <thead className='table-dark'>
                                    <tr>
                                        <th scope="col">Request date</th>
                                        <th scope="col">Requested by</th>
                                        <th scope="col">Status</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {this.state.logs.map((element, index) => (
                                        <tr key={index}>
                                            <td>{this.formatDate(element.date)}</td>
                                            <td>{element.first_name} {element.last_name}</td>
                                            {element.status == 200 ? (<td style={{color: 'green'}}>Success</td>) : (<td style={{color: 'red'}}>Failed</td>)}
                                        </tr>
                                    ))}
                                </tbody>
                            </Table>
                        </div>
                    )
                    :
                    (
                        <span style={{fontSize: '1.5rem', marginLeft: '5%', marginTop: '5px'}}>No recent activity</span>
                    )
                    }
                </ModalBody>
                <ModalFooter>
                    <Button color="primary" onClick={this.toggle}>CLOSE</Button>
                </ModalFooter>
            </Modal>
        </Container>
    </div>
    )
  }
}
